import express from 'express';
import { celebrate, Joi } from 'celebrate';
import resetPassword from '../controllers/reset_password_post';
import { baseUrl } from '../baseurl';

const router = express.Router();

const passwordChangeSchema = {
  body: Joi.object({
    uid: Joi.string().required(),
    newPassword: Joi.string().required(),
  }),
};
/**
 * @swagger
 * /auth/resetPassword:
 *   post:
 *     tags:
 *       - Authentication
 *     description: Returns status 200 if password is changed successfully
 *     consumes:
 *       - application/json
 *     produces:
 *       - application/json
 *     parameters:
 *       - in: "body"
 *         name: "uid"
 *         description: "UID for current password change action"
 *         required: true
 *         schema:
 *           type: object
 *           properties:
 *              uid :
 *                  type: string
 *              newPassword:
 *                  type: string
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *           properties:
 *              message:
 *                  type: string
 *       409:
 *         description: Returns 409 if the uid expires
 *         schema:
 *           type: object
 *           properties:
 *              statusCode:
 *                  type: number
 *                  example: 409
 *              error:
 *                  type: string
 *              message:
 *                  type: string
 *       400:
 *         description: Returns 400 if the uid is not valid
 *         schema:
 *           type: object
 *           properties:
 *              statusCode:
 *                  type: number
 *                  example: 400
 *              error:
 *                  type: string
 *              message:
 *                  type: string
 */

router.post(
  `${baseUrl}/resetPassword`,
  celebrate(passwordChangeSchema),
  resetPassword
);

export default router;
